
*{
  margin: 0;
  padding: 0;
}

header{
  background-color: blueviolet;
  display: flex;
  height: 12vh;
  justify-content: space-around;
  line-height: 12vh;
}

ul{
  display: flex;
  list-style: none;
  width: 300px;
  justify-content: space-around;

}

.main{
  height: 88vh;
  background-color: rgb(241, 176, 165);
  display: flex;
  justify-content: center;
  align-items: center;
}

.link{
  color: black;
  text-decoration: none;
}